*{
  box-sizing:border-box;
}
:root{
  --background:#100e2b;
  --text:#dedbfa;
  --primary:#867ced;
  --secondary:#971554;
  --accent:#e54b48;
  --shadow-color: 286deg 36% 70%; 
  --shadow-elevation-medium:
    0px 0.7px 0.9px hsl(var(--shadow-color) / 0.11),
    -0.1px 1.7px 2.1px -0.4px hsl(var(--shadow-color) / 0.13),
    -0.1px 3px 3.7px -0.9px hsl(var(--shadow-color) / 0.14),
    -0.2px 5.6px 6.9px -1.3px hsl(var(--shadow-color) / 0.16),
    -0.4px 10.2px 12.6px -1.8px hsl(var(--shadow-color) / 0.18);
    --s: 200px;
    --c1: #8c0058;
    --c2: #e61e2d;
    --c3: #00003c;
   
}
body,html{
  margin: 0;
  padding: 0;
  font-family: 'Karla',sans-serif;
  color: var(--text);
   background: repeating-conic-gradient(from 30deg,#0000 0 120deg,var(--c3) 0 180deg) calc(.5*var(--s)) calc(.5*var(--s)*0.577), repeating-conic-gradient(from 30deg,var(--c1) 0 60deg,var(--c2) 0 120deg,var(--c3) 0 180deg);
  background-size: var(--s) calc(var(--s)*0.577);
}
#root{

height: 100vh;
}
main{
max-width: 1020px;
width:50%;
background-color: var(--background)!important;
margin: 0 auto;
margin-top: 5em;
padding-right: 2em;
border-radius: 2em;
display:flex;
align-items: center;
flex-direction:column;
justify-content: center
}
h1{
margin-bottom: 0;
font-family: 'Karla';
font-style: normal;
font-weight: 700;
font-size: 34px;
line-height: 30px;
letter-spacing: 0.06em;
color: var(--primary);
text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
align-self: center;
}
h2{
font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 1.25rem;
line-height: 1.4;
letter-spacing: -0.06em;
color:var(--primary);
max-width: 20rem;
margin-right: auto;
margin-left: auto;
margin-bottom: 0;
}
.dice-con{
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  width:230px;
  height:130px;
  grid-gap: 0px ;
  margin-left:auto;
  margin-right:auto;
  padding-left: 2em;
}
.die{
height: 36px;
width: 36px;
border-radius: 4px;
padding:6px 14px;
box-shadow:var(--shadow-elevation-medium);
background-color: var(--accent); 
cursor:pointer;
}

.die-num{
  font-family: 'Karla';
  font-style: normal;
  font-weight: 700;
  font-size: 1.2rem;
  line-height: 24px;
  text-align: center;
}
.held{
background-color: var(--secondary);
color: #e9e9e9;
height: 36px;
width: 36px;
border-radius: 4px;
padding:6px 14px;
box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
}

button{
height: 40px;
min-width: 75px;
border-radius: 5px;
background-color:var(--orange);
box-shadow: 0px 4px 4px 0px #00000040;
cursor:pointer;
font-family: 'Karla';
font-style: normal;
font-weight: 700;
font-size: 1rem;
line-height: 15px;
background: var(--secondary);
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(5.9px);
-webkit-backdrop-filter: blur(5.9px);
border: 1px solid var(--text);

}
button:focus{
  outline:none;
  -webkit-animation: shake-horizontal 2s cubic-bezier(0.455, 0.030, 0.515, 0.955) 0.5 both;
  animation: shake-horizontal 2s cubic-bezier(0.455, 0.030, 0.515, 0.955) 0.5 both;
}
button:focus{
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.score-con{
  display: flex;
  flex-direction: column;
  align-self: end;
  padding-right: 8px;
  padding-bottom: 6px;
  font-weight: 400;
  color: var(--accent);
}
.last-score{
display: flex;
gap:8em;
padding-top:2em;
padding-bottom: 2em;
color: var(--secondary);
}
                                      /* animation */




 @-webkit-keyframes shake-horizontal {
  0%{
    background-position: 100% 50%;
    background: var(--secondary);
  }
  0%,
  100% {
    background-position: 0% 50%;
    background: var(--accent);
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  10%,
  30%,
  50%,
  70% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  20%,
  40%,
  60% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  80% {
    -webkit-transform: translateX(8px);
            transform: translateX(8px);
  }
  90% {
    -webkit-transform: translateX(-8px);
            transform: translateX(-8px);
  }
}
@keyframes shake-horizontal {
  0%{
    background-position: 100% 50%;
    background: var(--secondary);
  }
  0%,
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    background-position: 0% 50%;
    background: var(--accent);
  }
  10%,
  30%,
  50%,
  70% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  20%,
  40%,
  60% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  80% {
    -webkit-transform: translateX(8px);
            transform: translateX(8px);
  }
  90% {
    -webkit-transform: translateX(-8px);
            transform: translateX(-8px);
  }
}

.swirl-in-fwd {
	-webkit-animation: swirl-in-fwd 0.6s ease-out both;
	        animation: swirl-in-fwd 0.6s ease-out both;
}

 @-webkit-keyframes swirl-in-fwd {
  0% {
    -webkit-transform: rotate(-540deg) scale(0);
            transform: rotate(-540deg) scale(0);
    opacity: 0;
  }
  100% {
    -webkit-transform: rotate(0) scale(1);
            transform: rotate(0) scale(1);
    opacity: 1;
  }
}
@keyframes swirl-in-fwd {
  0% {
    -webkit-transform: rotate(-540deg) scale(0);
            transform: rotate(-540deg) scale(0);
    opacity: 0;
  }
  100% {
    -webkit-transform: rotate(0) scale(1);
            transform: rotate(0) scale(1);
    opacity: 1;
  }
}
@media (prefers-reduced-motion){
  html{
    animation: none;
  }
}
@media (max-width:620px){
  main{
    width: 90%;
  }
}